export default ":root {\r\n  --primary: #ff758c;  /* Rose vif */\r\n  --secondary: #ff7eb3; /* Rose moyen */\r\n  --danger: #ff4d6d;   /* Rose foncé */\r\n  --light-pink: #ffccd5; /* Rose très clair */\r\n  --bg-gradient-start: #fff0f3; /* Début dégradé */\r\n  --bg-gradient-end: #ffccd5;   /* Fin dégradé */\r\n}\r\n\r\n/* Reset de base */\r\n\r\n* {\r\n  box-sizing: border-box;\r\n  margin: 0;\r\n  padding: 0;\r\n  font-family: 'Segoe UI', sans-serif;\r\n}\r\n\r\n:host {\r\n  display: block;\r\n  min-height: 100vh;\r\n  background: linear-gradient(135deg, var(--bg-gradient-start), var(--bg-gradient-end));\r\n  position: relative;\r\n  overflow: hidden;\r\n  padding: 20px;\r\n}\r\n\r\n/* Bulles animées roses */\r\n\r\n:host::before,\r\n:host::after {\r\n  content: \"\";\r\n  position: absolute;\r\n  border-radius: 50%;\r\n  opacity: 0.15;\r\n  -webkit-animation: float 10s infinite ease-in-out alternate;\r\n          animation: float 10s infinite ease-in-out alternate;\r\n  background: var(--secondary);\r\n}\r\n\r\n.container {\r\n  max-width: 600px;\r\n  margin: 2rem auto;\r\n  padding: 2rem;\r\n  background: rgba(255, 255, 255, 0.9);\r\n  border-radius: 15px;\r\n  box-shadow: 0 5px 20px rgba(255, 117, 140, 0.2);\r\n  position: relative;\r\n  z-index: 1;\r\n  -webkit-backdrop-filter: blur(5px);\r\n          backdrop-filter: blur(5px);\r\n}\r\n\r\n.title {\r\n  color: var(--primary);\r\n  text-align: center;\r\n  margin-bottom: 1.5rem;\r\n  font-size: 2.2rem;\r\n  text-shadow: 0 2px 4px rgba(255, 117, 140, 0.2);\r\n}\r\n\r\n.input-container {\r\n  display: flex;\r\n  gap: 10px;\r\n  margin-bottom: 2rem;\r\n}\r\n\r\n.task-input {\r\n  flex: 1;\r\n  padding: 12px;\r\n  border: 2px solid var(--light-pink);\r\n  border-radius: 8px;\r\n  font-size: 1rem;\r\n  transition: all 0.3s;\r\n  background: rgba(255, 255, 255, 0.8);\r\n}\r\n\r\n.task-input:focus {\r\n  border-color: var(--primary);\r\n  outline: none;\r\n  box-shadow: 0 0 0 3px rgba(255, 117, 140, 0.3);\r\n}\r\n\r\n.add-btn {\r\n  background: var(--primary);\r\n  color: white;\r\n  border: none;\r\n  padding: 0 1.5rem;\r\n  border-radius: 8px;\r\n  cursor: pointer;\r\n  transition: all 0.3s;\r\n  font-size: 1rem;\r\n  box-shadow: 0 2px 5px rgba(255, 117, 140, 0.4);\r\n}\r\n\r\n.add-btn:hover {\r\n  background: var(--danger);\r\n  transform: translateY(-2px);\r\n}\r\n\r\n.task-list {\r\n  list-style: none;\r\n}\r\n\r\n.task-list li {\r\n  display: flex;\r\n  justify-content: space-between;\r\n  align-items: center;\r\n  padding: 12px;\r\n  margin-bottom: 10px;\r\n  background: rgba(255, 255, 255, 0.7);\r\n  border-radius: 10px;\r\n  transition: all 0.3s;\r\n  border-left: 4px solid var(--primary);\r\n}\r\n\r\n.task-list li:hover {\r\n  transform: translateY(-3px);\r\n  box-shadow: 0 5px 12px rgba(255, 126, 179, 0.2);\r\n  background: white;\r\n}\r\n\r\n.completed span {\r\n  text-decoration: line-through;\r\n  color: #c9184a;\r\n  opacity: 0.7;\r\n}\r\n\r\n.delete-btn {\r\n  background: none;\r\n  border: none;\r\n  color: var(--danger);\r\n  cursor: pointer;\r\n  font-size: 1rem;\r\n  padding: 5px;\r\n  margin-left: 10px;\r\n  transition: all 0.2s;\r\n}\r\n\r\n.delete-btn:hover {\r\n  transform: scale(1.2);\r\n}\r\n\r\n.hand-animation {\r\n  position: fixed;\r\n  bottom: 20px;   /* Distance depuis le bas */\r\n  right: 20px;    /* Distance depuis la droite */\r\n  width: 200px;   /* Taille réduite */\r\n  z-index: 100;\r\n  opacity: 0.9;\r\n  pointer-events: none;\r\n\r\n  -webkit-animation: none;\r\n\r\n          animation: none; /* Pas de flottement */\r\n}\r\n\r\n@-webkit-keyframes float {\r\n  0%, 100% { transform: translateY(0); }\r\n  50% { transform: translateY(-15px); }\r\n}\r\n\r\n@keyframes float {\r\n  0%, 100% { transform: translateY(0); }\r\n  50% { transform: translateY(-15px); }\r\n}\r\n\r\n/* Bulles animées */\r\n\r\n:host::before {\r\n  width: 250px;\r\n  height: 250px;\r\n  background: var(--secondary);\r\n  top: -50px;\r\n  left: -50px;\r\n}\r\n\r\n:host::after {\r\n  width: 350px;\r\n  height: 350px;\r\n  background: var(--primary);\r\n  bottom: -100px;\r\n  right: -100px;\r\n  opacity: 0.1;\r\n}\r\n\r\n/*# sourceMappingURL=data:application/json;base64, */"